<template>
    <div class="atm-view">
        <v-card class="atm grey darken-4 pa-8" v-if="!processing">
            <div class="overline mb-4">
                A T H E N A
            </div>
            <v-list-item-title class="headline mb-5 flex-grow-1 fill-width" block>
                <div class="flex-grow-1">
                    <v-chip label outlined small class="mr-3">Cash</v-chip>
                    ${{ cash.toLocaleString() }}
                </div>
                <span>
                    <v-chip label outlined small class="mr-3">Bank</v-chip>
                    ${{ bank.toLocaleString() }}
                </span>
            </v-list-item-title>
            <v-card-text>
                <v-expansion-panels>
                    <v-expansion-panel class="grey darken-3" @click="setType('withdraw')">
                        <v-expansion-panel-header>
                            Withdraw Cash
                            <template v-slot:actions>
                                <v-icon small class="flex-grow-1">
                                    icon-chevron-down
                                </v-icon>
                            </template>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                            <v-text-field
                                :rules="withdrawRules"
                                prepend-icon="icon-plus"
                                class="flex-grow-1"
                                type="number"
                                label="Withdraw Amount"
                                v-model="withdrawAmount"
                            />
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                    <v-expansion-panel class="grey darken-3" @click="setType('deposit')">
                        <v-expansion-panel-header>
                            Deposit Cash
                            <template v-slot:actions>
                                <v-icon small class="flex-grow-1">
                                    icon-chevron-down
                                </v-icon>
                            </template>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                            <v-text-field
                                :rules="depositRules"
                                prepend-icon="icon-minus"
                                class="flex-grow-1"
                                type="number"
                                label="Deposit Amount"
                                v-model="depositAmount"
                            />
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                    <v-expansion-panel class="grey darken-3" @click="setType('transfer')">
                        <v-expansion-panel-header>
                            Wire Transfer
                            <template v-slot:actions>
                                <v-icon small class="flex-grow-1">
                                    icon-chevron-down
                                </v-icon>
                            </template>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content class="flex-column">
                            <v-text-field
                                :rules="idRules"
                                prepend-icon="icon-user"
                                class="flex-grow-1"
                                type="number"
                                label="User ID"
                                v-model="wireTransferID"
                            ></v-text-field>
                            <v-text-field
                                :rules="wireTransferRules"
                                prepend-icon="icon-attach_money"
                                class="flex-grow-1"
                                type="number"
                                label="Amount"
                                v-model="wireTransferAmount"
                            ></v-text-field>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-card-text>
            <v-card-actions>
                <v-btn
                    @click="exit"
                    class="mt-4 error--text text--lighten-2 flex-grow-1 block"
                    color="error lighten-2"
                    outlined
                    text
                >
                    <v-icon small>icon-exit</v-icon>
                </v-btn>
                <v-btn
                    @click="transact"
                    :disabled="isValid === false"
                    class="mt-4 green--text text--lighten-2 flex-grow-1 block"
                    color="green lighten-2"
                    outlined
                    text
                >
                    <v-icon small>icon-check</v-icon>
                </v-btn>
            </v-card-actions>
        </v-card>
        <v-card class="atm-loading grey darken-4 pa-8" v-else>
            <div class="overline">
                A T H E N A
            </div>
            <v-card-text>
                <center>
                    <div class="lds-grid mt-8">
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <p>Processing...</p>
                </center>
            </v-card-text>
            <v-card-actions>
                <v-btn @click="exit" class="error--text text--lighten-2" color="error lighten-2" outlined text>
                    <v-icon small>icon-exit</v-icon>
                </v-btn>
            </v-card-actions>
        </v-card>
    </div>
</template>

<style scoped lang="less">
.atm-view {
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-items: center;
    align-content: center;
    background: transparent !important;
    width: 100vw;
    height: 100vh;
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.atm {
    animation: fadein 0.2s;
    user-select: none;
    min-width: 800px;
}

.atm-loading {
    min-width: 800px;
    animation: fadein 0.75s;
    user-select: none;
}

::-webkit-scrollbar {
    width: 8px;
    background: rgba(20, 20, 20, 0.85);
    box-shadow: -1px 0px 0px 0px rgba(0, 0, 0, 0.25);
}

::-webkit-scrollbar-thumb {
    background: rgba(100, 100, 100, 1);
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.1);
}

::-webkit-scrollbar-thumb:hover {
    cursor: pointer !important;
}

[v-cloak] {
    display: none !important;
}

.lds-grid {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

.lds-grid div {
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #fff;
    animation: lds-grid 1.2s linear infinite;
}

.lds-grid div:nth-child(1) {
    top: 8px;
    left: 8px;
    animation-delay: 0s;
}

.lds-grid div:nth-child(2) {
    top: 8px;
    left: 32px;
    animation-delay: -0.4s;
}

.lds-grid div:nth-child(3) {
    top: 8px;
    left: 56px;
    animation-delay: -0.8s;
}

.lds-grid div:nth-child(4) {
    top: 32px;
    left: 8px;
    animation-delay: -0.4s;
}

.lds-grid div:nth-child(5) {
    top: 32px;
    left: 32px;
    animation-delay: -0.8s;
}

.lds-grid div:nth-child(6) {
    top: 32px;
    left: 56px;
    animation-delay: -1.2s;
}

.lds-grid div:nth-child(7) {
    top: 56px;
    left: 8px;
    animation-delay: -0.8s;
}

.lds-grid div:nth-child(8) {
    top: 56px;
    left: 32px;
    animation-delay: -1.2s;
}

.lds-grid div:nth-child(9) {
    top: 56px;
    left: 56px;
    animation-delay: -1.6s;
}

@keyframes lds-grid {
    0%,
    100% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
}
</style>

<script>
export default {
    name: 'atm',
    data() {
        return {
            cash: 0,
            bank: 0,
            withdrawAmount: 0,
            depositAmount: 0,
            type: null,
            wireTransferAmount: 0,
            wireTransferID: 0,
            idRules: [(v) => !!v || 'This field is required', (v) => v >= 0 || 'ID must be positive'],
            depositRules: [
                (v) => !!v || 'This field is required',
                (v) => v > 0 || 'Value must be greater than zero.',
                (v) => v <= this.cash || 'Amount must not be greater than your cash on hand.'
            ],
            withdrawRules: [
                (v) => !!v || 'This field is required',
                (v) => v > 0 || 'Value must be greater than zero.',
                (v) => v <= this.bank || 'Amount must not be greater than your bank account.'
            ],
            wireTransferRules: [
                (v) => !!v || 'This field is required',
                (v) => v > 0 || 'Value must be greater than zero.',
                (v) => v < this.bank || 'Amount must not be greater than your bank account.'
            ],
            isValid: false,
            processing: true
        };
    },
    watch: {
        withdrawAmount(newValue) {
            if (newValue <= 0 || newValue > this.bank) {
                this.isValid = false;
                return;
            }

            this.isValid = true;
        },
        depositAmount(newValue) {
            if (newValue <= 0 || newValue > this.cash) {
                this.isValid = false;
                return;
            }

            this.isValid = true;
        },
        wireTransferAmount(newValue) {
            if (newValue <= 0 || newValue > this.bank) {
                this.isValid = false;
                return;
            }

            this.isValid = true;
        }
    },
    methods: {
        setType(name) {
            this.type = name;
        },
        transact() {
            if (!this.isValid) return;
            let amount = 0;
            switch (this.type) {
                case 'deposit':
                    amount = this.depositAmount;
                    break;

                case 'withdraw':
                    amount = this.withdrawAmount;
                    break;

                case 'transfer':
                    amount = this.wireTransferAmount;
                    break;
            }

            this.processing = true;

            if ('alt' in window) {
                alt.emit('atm:Action', this.type, amount, this.wireTransferID);
            }
        },
        updateBalances(bank, cash) {
            this.bank = bank;
            this.cash = cash;

            setTimeout(() => {
                this.isValid = false;
                this.processing = false;
            }, 1000);
        },
        exit() {
            if ('alt' in window) {
                alt.emit('atm:Close');
            }
        }
    },
    mounted() {
        if ('alt' in window) {
            alt.on('atm:Update', this.updateBalances);
            alt.emit('atm:Ready');
            alt.emit('ready');
        } else {
            const cash = Math.floor(Math.random() * 5000000);
            const bank = Math.floor(Math.random() * 5000000);
            this.updateBalances(cash, bank);
        }
    }
};
</script>
