<template>
    <div class="hud">
        <div v-if="!leaderboard">
            <div class="chat">
                <div :class="!helpState ? { 'help-fill-bye': true } : { 'help-fill': true }"></div>
                <div :class="getHelpTextClasses">
                    <div class="help-row" v-if="helpText && helpText.key">
                        <div class="keypress">{{ String.fromCharCode(helpText.key) }}</div>
                        <div class="descriptors">
                            <div class="message" v-if="helpText.shortPress">Press - {{ helpText.shortPress }}</div>
                            <div class="message" v-if="helpText.longPress">Hold - {{ helpText.longPress }}</div>
                        </div>
                    </div>
                </div>
                <div class="messages" ref="messages">
                    <div class="message" v-for="(message, index) in messages" :index="index" :key="index">
                        <template v-if="timestamp">
                            <span>{{ message.time }}</span> <span :inner-html.prop="message.message | colorify" />
                        </template>
                        <template v-else>
                            <span :inner-html.prop="message.message | colorify" />
                        </template>
                    </div>
                </div>
                <div class="outerInput" v-if="active">
                    <div class="mockInput">{{ matchedCommand !== null ? matchedCommand : '' }}</div>
                    <input
                        class="chatInput"
                        label="Use '/' to type a command."
                        ref="chatInput"
                        v-model="currentMessage"
                        @keyup.enter="handleSend"
                        @keyup.escape="handleEscape"
                        @keyup.up="prevMessage"
                        @keyup.down="nextMessage"
                        @keyup.right="useCommand"
                        @keyup="handleTyping"
                        maxlength="128"
                        v-focus
                    />
                </div>
                <div class="outerInput" v-else>
                    <input class="chatInput-hidden" ref="chatInput" disabled />
                </div>
            </div>
        </div>
        <div v-else>
            <v-list class="leaderboard elevation-8 pa-5" dense>
                <v-subheader>
                    <v-row class="flex-grow-1">
                        <v-col><span class="text-left">ID</span></v-col>
                        <v-col><span class="text-left">Player Name(s)</span></v-col>
                        <v-col><span class="text-left">Distance</span></v-col>
                        <v-col><span class="text-left">Ping</span></v-col>
                    </v-row>
                </v-subheader>
                <v-list-item-group class="group">
                    <v-list-item v-for="(player, index) in players" :key="index">
                        <v-row class="flex-grow-1">
                            <v-col
                                ><span class="flex-grow-1 text-left">{{ player.id }}</span></v-col
                            >
                            <v-col
                                ><span class="flex-grow-1 text-left">{{ player.name }}</span></v-col
                            >
                            <v-col
                                ><span class="flex-grow-1 text-left">{{ player.distance.toFixed(2) }}</span></v-col
                            >
                            <v-col
                                ><span class="flex-grow-1 text-left"
                                    ><v-icon>icon-wifi</v-icon> {{ player.ping }}ms</span
                                ></v-col
                            >
                        </v-row>
                    </v-list-item>
                </v-list-item-group>
            </v-list>
        </div>
    </div>
</template>

<style scoped lang="less">
.hud {
    left: 0;
    top: 0;
    position: fixed;
    background: transparent !important;
    width: 100vw;
    max-width: 100vw;
    max-height: 100vh;
}

.chat {
    display: flex;
    flex-direction: column;
    min-width: 400px;
    max-width: 400px;
    overflow: hidden;
    margin-left: 12px;
    margin-top: 12px;

    .chatInput-hidden {
        opacity: 0;
    }

    .chatInput {
        text-shadow: 1px 1px black;
        display: block;
    }

    .help-fill {
        position: absolute;
        min-height: 58px;
        min-width: 400px;
        max-height: 58px;
        animation: fadebgin 0.8s ease-out !important;
        box-shadow: inset 0px -58px 0 0.01px rgba(255, 255, 255, 0.5);
        z-index: 3;
    }

    .help-fill-bye {
        position: absolute;
        min-height: 58px;
        min-width: 400px;
        max-height: 58px;
        box-shadow: inset 0px 0px 0 0.01px rgba(255, 255, 255, 0.5);
        animation: fadebgout 0.2s ease-out !important;
        z-index: 3;
    }

    .help-row-wrapper-null {
        min-height: 58px;
        min-width: 200px;
        max-height: 58px;
        display: flex;
        flex-direction: row;
        box-sizing: border-box;
        background: rgba(0, 0, 0, 0.75);
        margin-bottom: 1.5vh;
        padding: 5px;
        justify-content: left;
        align-content: center;
        opacity: 0;
        align-items: center;
        animation: fadeout 0.5s;
        z-index: 2;
    }

    .help-row-wrapper {
        min-height: 58px;
        min-width: 200px;
        max-height: 58px;
        display: flex;
        flex-direction: row;
        box-sizing: border-box;
        background: rgba(0, 0, 0, 0.75);
        margin-bottom: 1.5vh;
        padding: 5px;
        justify-content: left;
        align-content: center;
        align-items: center;
        animation: fadein 0.2s;
        z-index: 2;

        .help-row {
            display: flex;
            flex-direction: row;
            align-content: center;
            align-items: center;
            color: white;
            font-weight: 600;
            animation: fadein 1s;
            user-select: none;
            overflow: hidden;
            text-overflow: ellipsis;
            height: 58px;

            .descriptors {
                display: flex;
                flex-direction: column;

                .message {
                    font-size: 13px;
                    margin-left: 12px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }

            .keypress {
                display: flex;
                height: 35px;
                width: 35px;
                margin-left: 6px;
                justify-items: center;
                justify-content: center;
                align-content: center;
                align-items: center;
                background: rgba(255, 255, 255, 0.8);
                box-sizing: border-box;
                border-radius: 5px;
                color: rgba(0, 0, 0, 0.65);
                font-size: 24px;
                border: 2px solid rgba(0, 0, 0, 0.45);
            }
        }
    }

    .messages {
        direction: rtl;
        overflow: auto;
        min-height: 35vh;
        max-height: 35vh;
        user-select: none;

        .message {
            color: white;
            text-shadow: 1px 1px black;
            direction: ltr;
            padding-left: 6px;
            margin-bottom: 6px;
            user-select: none;
        }
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeout {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes fadebgin {
    0% {
        box-shadow: inset 0px 0 0 0.01px rgba(255, 255, 255, 0.5);
    }
    100% {
        box-shadow: inset 0px -58px 0 0.01px rgba(255, 255, 255, 0.5);
    }
}

@keyframes fadebgout {
    0% {
        box-shadow: inset 0px -58px 0 0.01px rgba(255, 255, 255, 0.5);
        opacity: 1;
    }
    50% {
        box-shadow: inset 0px -58px 0 0.01px rgba(255, 255, 255, 0.95);
        opacity: 1;
    }
    100% {
        opacity: 0;
        box-shadow: inset 0px 0 0 0.01px rgba(255, 255, 255, 0);
    }
}

.outerInput {
    position: relative;
    top: 0;
    display: block;
    min-height: 50px;
    max-height: 50px;
    flex-grow: 1;
}

input {
    position: absolute;
    color: white;
    text-shadow: 1px 1px black;
    background: rgba(0, 0, 0, 0.75);
    margin-top: 12px;
    padding: 5px;
    outline: none;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 30px;
    max-height: 30px;
}

.mockInput {
    position: absolute;
    color: white;
    width: 100%;
    height: 100%;
    margin-top: 10px;
    padding: 5px;
    display: block;
    top: 0;
    left: 0;
    min-height: 30px;
    max-height: 30px;
    z-index: 99;
    opacity: 0.4;
    user-select: none;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    pointer-events: none;
}

.leaderboard {
    display: fixed;
    margin: 10vh auto;
    flex-grow: 1;
    max-height: calc(100% - 5vh);
    min-width: 40vw;
    max-width: 60vw;
    overflow: hidden;
    animation: fadein 0.2s;
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.v-list-item-group {
    overflow: auto;
    max-height: inherit;
}

::-webkit-scrollbar {
    width: 8px;
    background: rgba(20, 20, 20, 0.85);
    box-shadow: -1px 0px 0px 0px rgba(0, 0, 0, 0.25);
}

::-webkit-scrollbar-thumb {
    background: rgba(100, 100, 100, 1);
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.1);
}

::-webkit-scrollbar-thumb:hover {
    cursor: pointer !important;
}
</style>

<script>
import { tagOrBody, tagOrComment } from './shared/regex';

async function sleep(ms) {
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve();
        }, ms);
    });
}

export default {
    name: 'hud',
    data() {
        return {
            leaderboard: false,
            players: [],
            previous: [' ', 'alt:V Athena Chat', 'By Stuyk'],
            messages: [],
            commands: [
                { name: 'timestamp', description: '/timestamp - Toggles timestamps.' },
                { name: 'help', description: '/help - List all available commands for your permission level.' }
            ],
            currentMessage: '',
            active: false,
            position: 0,
            timestamp: false,
            matchedCommand: null,
            show: false,
            // Help Text
            helpText: null,
            helpTimeout: null,
            helpState: false
        };
    },
    computed: {
        getHelpTextClasses() {
            const data = {};

            if (this.helpText === null) {
                data['help-row-wrapper-null'] = true;
            } else {
                data['help-row-wrapper'] = true;
            }

            return data;
        }
    },
    methods: {
        appendMessage(msg) {
            const currentTime = Date.now();
            const date = new Date(currentTime);
            this.messages.push({
                message: msg,
                time: `[${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}]`
            });

            // Log Messages to Console
            if ('alt' in window) {
                console.log(`[${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}] ${msg}`);
            }

            if (this.messages.length >= 50) {
                this.messages.shift();
            }

            if (!this.show) {
                return;
            }

            if (!this.active) {
                this.$nextTick(() => {
                    if (!this.$refs || !this.$refs.messages) {
                        return;
                    }

                    this.$refs.messages.scrollTop = this.$refs.messages.scrollHeight;
                });
            }
        },
        trimDescription(description) {
            if (description.length <= 55) {
                return description;
            }

            return `${description.substr(0, 55)}...`;
        },
        focusChat() {
            this.active = true;
        },
        handleEscape() {
            this.active = false;
            this.position = 0;
            this.currentMessage = '';

            if ('alt' in window) {
                alt.emit('chat:Send');
            }
        },
        prevMessage() {
            if (this.position + 1 > this.previous.length - 1) {
                return;
            }

            this.position += 1;
            this.currentMessage = this.previous[this.position];
        },
        nextMessage() {
            if (this.position - 1 < 0) {
                return;
            }

            this.position -= 1;
            this.currentMessage = this.previous[this.position];
        },
        handleSend(e) {
            const message = e.target.value;
            this.active = false;
            this.position = 0;
            this.currentMessage = '';

            // Handle No Message
            if (!message || message === '') {
                if ('alt' in window) {
                    alt.emit('chat:Send');
                }
                return;
            }

            if (message === '/timestamp') {
                this.timestamp = !this.timestamp;
                this.appendMessage(`You have toggled timestamps.`);

                if ('alt' in window) {
                    alt.emit('chat:Send');
                }
                return;
            }

            if (message === '/help' || message === '/commands') {
                for (let i = 0; i < this.commands.length; i++) {
                    this.appendMessage(`${this.commands[i].description}`);
                }

                if ('alt' in window) {
                    alt.emit('chat:Send');
                }
                return;
            }

            // Appends message to front of array.
            if (!this.previous.includes(message)) {
                this.previous = this.previous.filter((x) => x !== '');
                this.previous.unshift(message);
                this.previous.unshift('');
            }

            // Handle Send Message
            if ('alt' in window) {
                alt.emit('chat:Send', message);
            }
        },
        handleTyping(e) {
            this.currentMessage = this.currentMessage.replace(tagOrComment, '').replace('/</g', '&lt;');

            if (this.currentMessage === '' || this.currentMessage.length <= 2) {
                this.matchedCommand = null;
                return;
            }

            const index = this.commands.findIndex(
                (x) => x && x.description && x.description.includes(this.currentMessage)
            );

            if (index <= -1) {
                this.matchedCommand = null;
                return;
            }

            if (this.matchedCommand !== null && this.currentMessage.replace('/', '') === this.commands[index].name) {
                this.matchedCommand = `/${this.commands[index].name}`;
                return;
            }

            this.matchedName = this.commands[index].name;
            this.matchedCommand = this.trimDescription(this.commands[index].description);
        },
        useCommand() {
            if (this.matchedCommand && this.matchedName) {
                this.currentMessage = `/${this.matchedName}`;
            }
        },
        populateCommands(commands) {
            if (!Array.isArray(commands)) {
                return;
            }

            this.commands = this.commands.concat(commands);
        },
        toggleLeaderboard(validPlayers) {
            this.players = validPlayers;
            this.leaderboard = !this.leaderboard;

            if ('alt' in window) {
                alt.emit('mouse:Focus', this.leaderboard);
            }
        },
        audio3D(soundName, pan, volume, duration = -1) {
            if (!duration) {
                duration = -1;
            }

            const audio = new Audio(require(`@/assets/sounds/${soundName}.mp3`));
            const ambientContext = new AudioContext();
            const source = ambientContext.createMediaElementSource(audio);
            const ambientPan = ambientContext.createStereoPanner();
            source.connect(ambientPan);
            ambientPan.connect(ambientContext.destination);
            ambientPan.pan.value = pan;

            if (duration >= 0) {
                audio.loop = true;
            } else {
                audio.loop = false;
            }

            audio.volume = volume;
            audio.autoplay = true;

            try {
                audio.play();
            } catch (err) {}

            if (duration >= 0) {
                setTimeout(() => {
                    audio.pause();
                }, duration);
            }
        },
        setHelpText(key, shortPress, longPress = null) {
            if (!key && this.helpText) {
                this.helpText = null;
                return;
            }

            if (this.helpTimeout) {
                clearTimeout(this.helpTimeout);
                this.helpTimeout = null;
            }

            this.helpTimeout = setTimeout(this.manageHelpTimeout, 1000);

            if (!this.helpText || this.helpText.shortPress !== shortPress || this.helpText.longPress !== longPress) {
                this.helpText = { key, shortPress, longPress };
            }
        },
        manageHelpTimeout() {
            this.helpText = null;
        },
        setHelpState(value) {
            this.helpState = value;
        }
    },
    directives: {
        focus: {
            inserted: (el) => {
                el.focus();
            }
        }
    },
    watch: {
        currentMessage: (newValue) => {
            this.currentMessage = newValue;
        }
    },
    filters: {
        colorify(text) {
            let matches = [];
            let m = null;
            let curPos = 0;

            if (!text) {
                return;
            }

            do {
                m = /\{[A-Fa-f0-9]{3}\}|\{[A-Fa-f0-9]{6}\}/g.exec(text.substr(curPos));

                if (!m) {
                    break;
                }

                matches.push({
                    found: m[0],
                    index: m['index'] + curPos
                });

                curPos = curPos + m['index'] + m[0].length;
            } while (m != null);

            if (matches.length > 0) {
                text += '</font>';

                for (let i = matches.length - 1; i >= 0; --i) {
                    let color = matches[i].found.substring(1, matches[i].found.length - 1);
                    let insertHtml = `${i !== 0 ? '</font>' : ''}<font color="#${color}">`;
                    text = `${text.slice(0, matches[i].index)}${insertHtml}${text.slice(
                        matches[i].index + matches[i].found.length,
                        text.length
                    )}`;
                }
            }

            return text;
        }
    },
    mounted() {
        if ('alt' in window) {
            alt.on('chat:Append', this.appendMessage);
            alt.on('chat:Focus', this.focusChat);
            alt.on('chat:PopulateCommands', this.populateCommands);
            alt.on('leaderboard:Toggle', this.toggleLeaderboard);
            alt.on('hud:Audio3D', this.audio3D);
            alt.on('hud:HelpText', this.setHelpText);
            alt.on('hud:HelpState', this.setHelpState);
        } else {
            let count = 0;
            setInterval(() => {
                count += 1;
                this.appendMessage(
                    `Message ${count} lore impsum do stuff long words holy moley loook at my nice long sentence.`
                );
            }, 100);

            setTimeout(() => {
                this.focusChat();
            }, 1000);

            this.players = [
                { name: 'Johnny_Joe', ping: 25, id: 0, distance: 25 },
                { name: 'Jobi_Jobanni', ping: 60, id: 1, distance: 30 }
            ];

            for (let i = 0; i < 5; i++) {
                this.players = [...this.players, ...this.players];
            }

            this.setHelpText(69, 'Short press description go brrr');

            (async () => {
                this.setHelpState(true);
                await sleep(1500);
                this.setHelpState(false);
            })();

            setInterval(async () => {
                this.setHelpText(69, 'Short press description go brrr', 'Long press description go brrr');

                await sleep(1000);
                this.setHelpState(true);
                await sleep(1000);
                this.setHelpState(false);
            }, 7000);

            // setInterval(() => {
            //     this.audio3D('car_lock', 0, 1);
            // }, 2000);
        }

        this.$nextTick(() => {
            this.show = true;
        });
    }
};
</script>
