<template>
    <div class="creator">
        <v-container class="panel pl-0 pt-0 fill-height transparent">
            <div class="special-bar d-flex flex-column justify-space-between fill-height grey darken-4">
                <v-navigation-drawer permanent expand-on-hover class="grey darken-4">
                    <v-list>
                        <v-list-item
                            right
                            v-for="(option, index) in navOptions"
                            :key="index"
                            link
                            @click="goNavigate(index)"
                            :disabled="!validInfoData && index === navOptions.length - 1"
                        >
                            <template v-if="index !== navOptions.length - 1">
                                <v-list-item-icon>
                                    <v-icon class="light-blue--text text--accent-2">{{
                                        navOptionsIcons[index].icon
                                    }}</v-icon>
                                </v-list-item-icon>
                                <v-list-item-title class="light-blue--text text--accent-1">
                                    {{ navOptionsTitles[index] }}
                                </v-list-item-title>
                            </template>
                            <template v-else>
                                <v-list-item-icon>
                                    <v-icon
                                        :class="validInfoData ? 'green--text text--accent-2' : ''"
                                        :disabled="!validInfoData"
                                        >{{ navOptionsIcons[index].icon }}</v-icon
                                    >
                                </v-list-item-icon>
                                <v-list-item-title
                                    :class="validInfoData ? 'green--text text--accent-2' : ''"
                                    :disabled="!validInfoData"
                                >
                                    {{ navOptionsTitles[index] }}
                                </v-list-item-title>
                            </template>
                        </v-list-item>
                    </v-list>
                </v-navigation-drawer>
            </div>
            <v-sheet
                class="special-box d-flex flex-row pl-0 pr-0 align-content-center grey darken-4"
                max-height="100vh"
                min-height="100vh"
                max-width="400px"
            >
                <component
                    v-bind:is="getTabComponent"
                    v-bind:data="data"
                    v-bind:nodiscard="noDiscard"
                    v-bind:noname="noName"
                    v-bind:currentname="data.name"
                    v-bind:infodata="infoData"
                    v-bind:totalcharacters="totalCharacters"
                >
                </component>
            </v-sheet>
        </v-container>
    </div>
</template>

<style scoped lang="less">
*,
*:focus {
    outline: none;
    user-select: none !important;
}

.creator {
    display: flex;
    justify-content: left;
    align-items: center;
    align-content: center;
    width: 100vw;
    overflow: hidden;
    position: relative;
    animation: fadein 2s;
    font-family: 'Inter';
}

.panel {
    margin: 0 !important;
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

#app {
    opacity: 1;
}

v-card {
    min-height: 85px;
}

.special-box {
    min-width: 400px;
    height: 100vh;
}

.containerHelper {
    overflow-y: auto;
    overflow-x: hidden;
}

.spinner {
    position: absolute;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}

::-webkit-scrollbar {
    width: 8px;
    background: rgba(20, 20, 20, 0.85);
    box-shadow: -1px 0px 0px 0px rgba(0, 0, 0, 0.25);
}

::-webkit-scrollbar-thumb {
    background: rgba(100, 100, 100, 1);
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.1);
}

::-webkit-scrollbar-thumb:hover {
    cursor: pointer !important;
}
</style>

<script>
import tabSex from './components/Sex';
import tabStructure from './components/Structure';
import tabHair from './components/Hair';
import tabOverlays from './components/Overlays';
import tabMakeup from './components/Makeup';
import tabInfo from './components/Info';
import tabDone from './components/Done';

import { overlaysTemplateList, colorOverlays } from './shared/overlays';
import { maleHairOverlays, femaleHairOverlays } from './shared/hairOverlays';

export default {
    name: 'creator',
    components: {
        'tab-sex': tabSex,
        'tab-structure': tabStructure,
        'tab-hair': tabHair,
        'tab-overlays': tabOverlays,
        'tab-makeup': tabMakeup,
        'tab-info': tabInfo,
        'tab-done': tabDone
    },
    data() {
        return {
            show: false,
            selection: 0,
            data: {
                sex: 1,
                faceMother: 0,
                faceFather: 0,
                skinMother: 0,
                skinFather: 0,
                skinMix: 0.5,
                faceMix: 0.5,
                structure: new Array(20).fill(0),
                hair: 3,
                hairColor1: 1,
                hairColor2: 5,
                hairOverlay: '',
                facialHair: 29,
                facialHairColor1: 0,
                facialHairOpacity: 0,
                eyebrows: 0,
                eyebrowsOpacity: 1,
                eyebrowsColor1: 0,
                eyes: 0,
                opacityOverlays: [],
                colorOverlays: []
            },
            infoData: {
                age: new Date().toISOString().substr(0, 10),
                gender: 'none',
                name: 'John_Dane'
            },
            navOptions: ['Sex', 'Structure', 'Hair', 'Overlays', 'Makeup', 'Info', 'Done'],
            navOptionsIcons: [
                { icon: 'icon-orientation' },
                { icon: 'icon-cogs' },
                { icon: 'icon-hair' },
                { icon: 'icon-details1' },
                { icon: 'icon-makeup' },
                { icon: 'icon-id-card' },
                { icon: 'icon-check' }
            ],
            navOptionsTitles: ['Appearance', 'Structure', 'Hair', 'Details', 'Makeup', 'Info', 'Done'],
            noDiscard: false,
            noName: false,
            validInfoData: false,
            drawer: true,
            mini: true,
            totalCharacters: 1
        };
    },
    computed: {
        isInactive() {
            if (this.selection >= this.navOptions.length - 1) {
                return true;
            }

            if (this.selection === 5 && !this.noName && !this.validInfoData) {
                return true;
            }

            return false;
        },
        isInactiveNext() {
            if (this.selection >= this.navOptions.length - 1) {
                return true;
            }

            if (this.selection === 5 && !this.noName && !this.validInfoData) {
                return true;
            }

            return false;
        },
        isInactiveBack() {
            if (this.selection <= 0) {
                return true;
            }

            return false;
        },
        getTabComponent: function() {
            return `tab-${this.navOptions[this.selection].toLowerCase()}`;
        }
    },
    methods: {
        setReady(noDiscard = true, noName = true) {
            if (this.show) {
                return;
            }

            this.noDiscard = noDiscard;
            this.noName = noName;
            this.show = true;

            if ('alt' in window) {
                alt.emit('creator:ReadyDone');
            }
        },
        setData(oldData, totalCharacters) {
            this.totalCharacters = totalCharacters;

            if (!oldData) {
                this.updateCharacter();
                return;
            }

            this.data = oldData;
            this.updateCharacter();
        },
        goNavigate(value) {
            this.selection = value;

            // Info
            if ('alt' in window) {
                alt.emit('creator:DisableControls', this.selection === 5 ? true : false);
            }
        },
        updateCharacter() {
            const isFemale = this.data.sex === 0;
            this.data.hairOverlay = isFemale ? femaleHairOverlays[this.data.hair] : maleHairOverlays[this.data.hair];

            if (isFemale) {
                this.data.facialHair = 30;
                this.data.facialHairOpacity = 0;
            }

            // Update Floats
            this.data.skinMix = parseFloat(this.data.skinMix);
            this.data.faceMix = parseFloat(this.data.faceMix);

            if ('alt' in window) {
                alt.emit('creator:Sync', this.data, true);
            }
        },
        resetSelection() {
            this.selection = 0;
        },
        isVerified(isValid) {
            this.validInfoData = isValid;
        }
    },
    mounted() {
        this.$root.$on('updateCharacter', this.updateCharacter);
        this.$root.$on('resetSelection', this.resetSelection);
        this.$root.$on('isVerified', this.isVerified);

        overlaysTemplateList.forEach((overlay) => {
            const overlayData = { ...overlay };
            overlayData.value = 0;
            delete overlayData.key;
            delete overlayData.max;
            delete overlayData.min;
            delete overlayData.label;
            delete overlayData.increment;

            this.data.opacityOverlays.push(overlayData);
        });

        colorOverlays.forEach((overlay) => {
            const overlayData = { ...overlay };
            overlayData.value = 0;
            delete overlayData.key;
            delete overlayData.max;
            delete overlayData.min;
            delete overlayData.label;
            delete overlayData.increment;

            this.data.colorOverlays.push(overlayData);
        });

        this.$nextTick(() => {
            if ('alt' in window) {
                alt.on('creator:Ready', this.setReady);
                alt.on('creator:SetData', this.setData);
                alt.emit('ready');
            } else {
                this.show = true;
            }
        });

        console.log(`Loaded Character Creator`);
    }
};
</script>
