<template>
    <div class="inventory">
        <div class="main-container">
            <div class="player rounded">
                <div class="toolbar grey darken-4 elevation-6">
                    <div
                        class="rounded grey darken-2"
                        v-for="(item, index) in toolbar"
                        :id="`t-${index}`"
                        :class="!item ? { 'is-null-item': true } : { item: true }"
                        :key="index"
                        v-on="
                            !item
                                ? null
                                : { mousedown: selectItem, mouseenter: selectItemInfo, mouseleave: setItemInfo }
                        "
                    >
                        <template v-if="item">
                            <div class="icon no-pointer">
                                <img :src="require(`@/assets/icons/${item.icon}.svg`)" />
                            </div>
                            <div class="stats no-pointer">
                                <div class="quantity">{{ item.quantity }}x</div>
                                <div class="weight">{{ item.weight }} lb</div>
                            </div>
                        </template>
                        <template v-else>
                            <div class="grey--text text--darken-2 no-pointer">
                                {{ index + 1 }}
                            </div>
                        </template>
                    </div>
                </div>
                <div class="equipment grey darken-4 elevation-6">
                    <div
                        class="rounded grey darken-2"
                        v-for="(item, index) in equipment"
                        :id="`e-${index}`"
                        :class="!item ? { 'is-null-item': true } : { item: true }"
                        :key="index"
                        v-on="
                            !item
                                ? null
                                : { mousedown: selectItem, mouseenter: selectItemInfo, mouseleave: setItemInfo }
                        "
                    >
                        <template v-if="item">
                            <div class="icon no-pointer">
                                <img :src="require(`@/assets/icons/${item.icon}.svg`)" />
                            </div>
                            <div class="stats no-pointer">
                                <div class="quantity">{{ item.quantity }}x</div>
                                <div class="weight">{{ item.weight }} lb</div>
                            </div>
                        </template>
                        <template v-else>
                            <div class="grey--text text--darken-2 no-pointer">
                                {{ equipmentMeta[index] }}
                            </div>
                        </template>
                    </div>
                </div>

                <template v-if="itemInfo && getItemInfo">
                    <div class="item-info grey darken-4">
                        <div class="name text-h5 font-weight-bold light-blue--text text--lighten-3">
                            {{ getItemInfo.name }}
                        </div>
                        <v-divider class="mb-2"></v-divider>
                        <div class="quantity text-caption">
                            <v-chip label color="grey darken-3" small>Quantity: {{ getItemInfo.quantity }}x</v-chip>
                        </div>
                        <v-divider class="mt-2 mb-2"></v-divider>
                        <div class="description text-subtitle-2 grey--text text--lighten-2">
                            {{ getItemInfo.description }}
                        </div>
                        <template v-if="getItemInfo.data">
                            <v-divider class="mt-2 mb-2"></v-divider>
                            <div class="name text-sm font-weight-bold light-blue--text text--lighten-4">
                                Data
                            </div>
                            <v-divider class="mt-2 mb-2"></v-divider>
                            <div class="chips">
                                <v-chip
                                    v-for="(prop, index) in getItemProperties"
                                    :key="index"
                                    class="mb-2 mr-2 ml-2 flex-grow-1 text-uppercase"
                                    label
                                    color="blue-grey darken-3"
                                    small
                                >
                                    {{ prop.key }}: {{ prop.value }}
                                </v-chip>
                            </div>
                        </template>
                    </div>
                </template>
            </div>
            <div class="split-view">
                <div
                    class="inventory grey darken-4 rounded elevation-6"
                    :class="ground.length <= 0 ? { expand: true } : {}"
                >
                    <div class="inventory-list" :class="getInventoryClass()">
                        <div
                            class="rounded grey darken-3"
                            v-for="(item, index) in getInventory"
                            :id="`i-${index}`"
                            :key="index"
                            :class="!item ? { 'is-null-item': true } : { item: true }"
                            v-on="
                                !item
                                    ? null
                                    : { mousedown: selectItem, mouseenter: selectItemInfo, mouseleave: setItemInfo }
                            "
                        >
                            <template v-if="item">
                                <div class="icon no-pointer">
                                    <img :src="require(`@/assets/icons/${item.icon}.svg`)" />
                                </div>
                                <div class="stats no-pointer">
                                    <div class="quantity">{{ item.quantity }}x</div>
                                    <div class="weight">{{ item.weight }} lb</div>
                                </div>
                            </template>
                        </div>
                    </div>
                </div>
                <div class="ground grey darken-4 rounded elevation-6" v-if="ground.length >= 1">
                    <div class="pickup-zone" block>
                        <div
                            class="rounded grey darken-3 rounded"
                            v-for="(item, index) in ground"
                            :class="!item ? { 'is-null-item': true } : { item: true }"
                            :key="index"
                            :id="`g-${index}`"
                            :data-hash="item ? `${item.hash}` : ''"
                            v-on="
                                !item
                                    ? null
                                    : { mousedown: selectItem, mouseenter: selectItemInfo, mouseleave: setItemInfo }
                            "
                        >
                            <template v-if="item">
                                <div class="icon no-pointer">
                                    <img :src="require(`@/assets/icons/${item.icon}.svg`)" />
                                </div>
                                <div class="stats no-pointer">
                                    <div class="quantity">{{ item.quantity }}x</div>
                                    <div class="weight">{{ item.weight }} lb</div>
                                </div>
                            </template>
                        </div>
                    </div>
                </div>
                <div class="navigation mt-1">
                    <div
                        v-for="index in 5"
                        :key="index - 1"
                        :id="`tab-${index - 1}`"
                        :class="isActiveTab(index - 1)"
                        @click="setIndex(index - 1)"
                    >
                        <v-icon>{{ pageIcons[index - 1] }}</v-icon>
                        <div class="tooltip">
                            {{ pageMeta[index - 1] }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style scoped lang="less">
@item-dimensions: 75px;
@item-icon-dimensions: 32px;
@item-grid-gap: 9px;

.inventory {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: relative;
    font-family: 'Inter';
    color: white;
}

.no-animation {
    transition: none !important;
    animation: none !important;
}

.item-clone {
    position: fixed !important;
    display: flex !important;
    pointer-events: none !important;
    border-radius: 4px !important;
    background-color: #424242 !important;
    min-width: @item-dimensions;
    max-width: @item-dimensions;
    min-height: @item-dimensions;
    max-height: @item-dimensions;
    box-shadow: 0px 0px 5px 2px black;
    left: 0;
    right: 0;
    direction: ltr;
    cursor: pointer;
    border: 2px solid transparent;
    box-sizing: border-box !important;
    overflow: visible;
    z-index: 99;

    &:hover {
        background: rgba(255, 255, 255, 0.3) !important;
    }
}

.v-application .v-application--wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-items: center;
    align-items: center;
    align-content: center;
    background: transparent !important;
    width: 100vw !important;
    height: 100vh !important;
    box-sizing: border-box;
}

.no-pointer {
    pointer-events: none !important;
}

.main-container {
    display: flex;
    flex-direction: row;
    position: relative;

    .player {
        position: relative;
        max-height: 95vh;
        margin-right: @item-grid-gap * 2;
        overflow: hidden;

        .toolbar {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            box-sizing: border-box;
            user-select: none;
            padding: @item-grid-gap;
            margin-bottom: @item-grid-gap * 2;
            border-radius: 5px;

            .item {
                position: relative;
                cursor: pointer;
                border: 2px solid transparent;
                box-sizing: border-box !important;
                overflow: hidden;
                height: @item-dimensions;
                width: @item-dimensions;

                &:hover {
                    background: rgba(255, 255, 255, 0.3) !important;
                }
            }

            .is-null-item {
                position: relative;
                display: flex;
                justify-content: center;
                align-content: center;
                justify-items: center;
                align-items: center;
                cursor: pointer;
                border: 2px solid transparent !important;
                box-sizing: border-box !important;
                overflow: hidden;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.3) !important;
                border: 2px solid rgba(0, 0, 0, 0.5) !important;
                cursor: auto !important;
                height: @item-dimensions;
                width: @item-dimensions;
            }
        }

        .equipment {
            display: grid;
            box-sizing: border-box;
            direction: ltr;
            grid-template-columns: repeat(4, @item-dimensions);
            grid-auto-rows: @item-dimensions;
            grid-gap: @item-grid-gap;
            height: calc(@item-dimensions * 3 + @item-grid-gap * 4);
            padding-left: @item-grid-gap;
            padding-right: @item-grid-gap;
            padding-top: @item-grid-gap;
            user-select: none;
            border-radius: 5px;

            .item {
                position: relative;
                cursor: pointer;
                border: 2px solid transparent;
                box-sizing: border-box !important;
                overflow: hidden;
                height: @item-dimensions;
                width: @item-dimensions;

                span {
                    text-align: center;
                }

                &:hover {
                    background: rgba(255, 255, 255, 0.3) !important;
                }
            }

            .is-null-item {
                position: relative;
                display: flex;
                justify-content: center;
                align-content: center;
                justify-items: center;
                align-items: center;
                cursor: pointer;
                border: 2px solid transparent !important;
                box-sizing: border-box !important;
                overflow: hidden;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.3) !important;
                border: 2px solid rgba(0, 0, 0, 0.5) !important;
                cursor: auto !important;
                height: @item-dimensions;
                width: @item-dimensions;
            }
        }

        .item-info {
            position: absolute;
            display: flex;
            flex-direction: column;
            margin-top: @item-grid-gap * 2;
            box-sizing: border-box;
            width: 100%;
            border-radius: 5px;
            padding: @item-grid-gap;
            user-select: none;

            .chips {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
            }
        }
    }

    .split-view {
        display: flex;
        flex-grow: 1;
        min-width: calc(@item-dimensions * 4 + @item-grid-gap * 5);
        max-width: calc(@item-dimensions * 4 + @item-grid-gap * 5);
        flex-direction: column;
        overflow: hidden;
        max-height: 95vh;
        z-index: 50;

        .inventory {
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-content: flex-start;
            justify-items: flex-start;
            align-items: flex-start;
            height: calc(@item-dimensions * 7 + @item-grid-gap * 8);
            user-select: none;
            z-index: 50 !important;

            .inventory-list {
                display: grid;
                box-sizing: border-box;
                grid-template-columns: repeat(4, @item-dimensions);
                grid-auto-rows: @item-dimensions;
                grid-gap: @item-grid-gap;
                height: calc(@item-dimensions * 7 + @item-grid-gap * 8);
                user-select: none;
                width: 100%;
                height: 100%;
                padding-left: @item-grid-gap;
                padding-top: @item-grid-gap;
                padding-bottom: @item-grid-gap;
                overflow-y: scroll;

                .item {
                    position: relative;
                    cursor: pointer;
                    border: 2px solid transparent;
                    box-sizing: border-box !important;
                    overflow: hidden;
                    height: 100%;

                    &:last-child {
                        margin-bottom: 100px !important;
                    }

                    &:hover {
                        background: rgba(255, 255, 255, 0.3) !important;
                    }
                }

                .is-null-item {
                    position: relative;
                    direction: ltr;
                    cursor: pointer;
                    border: 2px solid transparent !important;
                    box-sizing: border-box !important;
                    overflow: hidden;
                    height: 100%;
                    background-color: rgba(0, 0, 0, 0.3) !important;
                    border: 2px solid rgba(0, 0, 0, 0.5) !important;
                    cursor: auto !important;
                }

                .vip {
                    z-index: 99;
                }
            }
        }

        .ground {
            display: flex;
            flex-direction: column;
            margin-top: 2vh;
            min-height: calc(@item-dimensions * 2 + @item-grid-gap * 3);
            overflow: hidden;

            .pickup-zone {
                display: grid;
                overflow-y: scroll;
                min-height: calc(@item-dimensions * 2 + @item-grid-gap * 3);
                box-sizing: border-box;
                direction: rtl;
                grid-template-columns: repeat(4, @item-dimensions);
                grid-auto-rows: @item-dimensions;
                grid-gap: @item-grid-gap;
                user-select: none;
                width: 100%;
                padding-right: @item-grid-gap;
                padding-top: @item-grid-gap;

                .item {
                    position: relative;
                    min-height: @item-dimensions;
                    max-height: @item-dimensions;
                    background: black;
                    border: 2px solid transparent !important;
                    cursor: pointer;

                    &:hover {
                        background: rgba(255, 255, 255, 0.3) !important;
                    }
                }

                .is-null-item {
                    position: relative;
                    min-height: @item-dimensions;
                    max-height: @item-dimensions;
                    background-color: rgba(0, 0, 0, 0.3) !important;
                    border: 2px solid rgba(0, 0, 0, 0.5) !important;
                    cursor: auto;
                }
            }
        }
    }

    .navigation {
        max-width: 6vw;
        position: absolute;
        right: calc(-@item-dimensions + @item-grid-gap * 2);
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        z-index: 45;

        .tab {
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: @item-dimensions / 1.25;
            height: @item-dimensions / 1.25;
            z-index: 1 !important;
            outline: none;
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
            cursor: pointer;

            .tooltip {
                display: none;
                position: absolute;
            }

            &:hover {
                background: rgba(85, 85, 85, 1) !important;

                .tooltip {
                    display: flex;
                    align-content: flex-start;
                    position: absolute;
                    right: 0 - @item-dimensions * 3.05;
                    min-width: @item-dimensions * 3;
                    text-shadow: 1px 1px black;
                    text-align: left !important;
                }
            }

            i {
                pointer-events: none;
            }
        }
    }
}

.item .weight {
    position: absolute;
    text-shadow: 1px 1px black;
    font-size: 10px;
    color: white;
    right: 0;
    bottom: 0;
}

.item .quantity {
    position: absolute;
    text-shadow: 1px 1px black;
    font-size: 10px;
    color: white;
    top: 0;
    left: 0;
}

.icon {
    display: flex;
    justify-content: center;
    align-content: center;
    position: absolute;
    width: 100%;
    height: 100%;
    font-family: 'Inter';

    img {
        min-width: @item-dimensions / 1.75;
        max-width: @item-dimensions / 1.75;
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

::-webkit-scrollbar {
    width: 8px;
    background: rgba(20, 20, 20, 0.85);
    box-shadow: -1px 0px 0px 0px rgba(0, 0, 0, 0.25);
}

::-webkit-scrollbar-thumb {
    background: rgba(100, 100, 100, 1);
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.1);
}

::-webkit-scrollbar-thumb:hover {
    cursor: pointer !important;
}

[v-cloak] {
    display: none !important;
}

.lds-grid {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

.lds-grid div {
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #fff;
    animation: lds-grid 1.2s linear infinite;
}

.lds-grid div:nth-child(1) {
    top: 8px;
    left: 8px;
    animation-delay: 0s;
}

.lds-grid div:nth-child(2) {
    top: 8px;
    left: 32px;
    animation-delay: -0.4s;
}

.lds-grid div:nth-child(3) {
    top: 8px;
    left: 56px;
    animation-delay: -0.8s;
}

.lds-grid div:nth-child(4) {
    top: 32px;
    left: 8px;
    animation-delay: -0.4s;
}

.lds-grid div:nth-child(5) {
    top: 32px;
    left: 32px;
    animation-delay: -0.8s;
}

.lds-grid div:nth-child(6) {
    top: 32px;
    left: 56px;
    animation-delay: -1.2s;
}

.lds-grid div:nth-child(7) {
    top: 56px;
    left: 8px;
    animation-delay: -0.8s;
}

.lds-grid div:nth-child(8) {
    top: 56px;
    left: 32px;
    animation-delay: -1.2s;
}

.lds-grid div:nth-child(9) {
    top: 56px;
    left: 56px;
    animation-delay: -1.6s;
}

@keyframes lds-grid {
    0%,
    100% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
}
</style>

<script>
export default {
    name: 'inventory',
    data() {
        return {
            x: 0,
            y: 0,
            itemInfo: null,
            dragAndDrop: {
                shiftX: null,
                shiftY: null,
                clonedElement: null,
                itemIndex: null,
                selectedElement: null
            },
            pageIndex: 0,
            pageMeta: ['Tab 1', 'Tab 2', 'Tab 3', 'Tab 4', 'Tab 5'],
            pageIcons: ['icon-box', 'icon-box', 'icon-box', 'icon-box', 'icon-box'],
            equipmentMeta: ['Hat', 'Mask', 'Shirt', 'Pants', 'Feet', 'Glasses', 'Ears', 'Bag', 'Armour'],
            inventory: [[], [], [], [], [], []],
            ground: [],
            equipment: [],
            toolbar: []
        };
    },
    methods: {
        /**
         * List of items to append to the inventory data.
         * @param {Array<Object>} items
         */
        updateInventory(inventoryItems) {
            const newInventory = new Array(6);
            for (let i = 0; i < newInventory.length; i++) {
                newInventory[i] = new Array(28).fill(null);
            }

            // inventoryItems is an array of arrays.
            // The first array is the pageIndex or tab.
            for (let i = 0; i < inventoryItems.length; i++) {
                const tabIndex = i;
                for (let x = 0; x < inventoryItems[tabIndex].length; x++) {
                    if (!inventoryItems[tabIndex][x]) {
                        continue;
                    }

                    const slot = inventoryItems[tabIndex][x].slot;
                    newInventory[tabIndex][slot] = inventoryItems[tabIndex][x];
                }
            }

            this.inventory = newInventory;
        },
        updateGround(groundItems) {
            const newGround = new Array(8).fill(null);
            groundItems.forEach((item) => {
                if (!item) {
                    return;
                }

                newGround[item.slot] = item;
            });

            this.ground = newGround;
        },
        updateEquipment(equipmentItems) {
            const newEquipment = new Array(9).fill(null);
            equipmentItems.forEach((item) => {
                if (!item) {
                    return;
                }

                newEquipment[item.slot] = item;
            });

            this.equipment = newEquipment;
        },
        updateToolbar(toolbarItems) {
            const newToolbar = new Array(4).fill(null);

            toolbarItems.forEach((item) => {
                if (!item) {
                    return;
                }

                newToolbar[item.slot] = item;
            });

            this.toolbar = newToolbar;
        },
        updateGround(groundItems) {
            const newGround = new Array(8).fill(null);

            if (groundItems.length <= 0) {
                this.ground = newGround;
                return;
            }

            groundItems.forEach((groundItem, index) => {
                if (index >= newGround.length) {
                    return;
                }

                if (!groundItem) {
                    return;
                }

                newGround[index] = groundItem.item;
            });

            this.ground = newGround;
        },
        setIndex(value) {
            this.pageIndex = value;
        },
        isActiveTab(index) {
            return this.pageIndex === index
                ? { 'light-blue': true, tab: true, 'mb-1': true, 'elevation-6': true }
                : { grey: true, 'darken-4': true, tab: true, 'mb-1': true, 'elevation-6': true };
        },
        getInventoryClass() {
            const classList = {};

            if (this.ground.length <= 0) {
                classList['expand-list'] = true;
            }

            return classList;
        },
        selectItemInfo(e) {
            if (this.dragging) {
                return;
            }

            if (!e || !e.target || !e.target.id) {
                return;
            }

            this.itemInfo = e.target.id;
        },
        setItemInfo() {
            this.itemInfo = null;
        },
        selectItem(e, index) {
            if (this.dragging) {
                return;
            }

            this.dragging = true;

            // Calculate Element Size
            const element = document.getElementById(e.target.id);
            this.dragAndDrop.shiftX = e.clientX - element.getBoundingClientRect().left;
            this.dragAndDrop.shiftY = e.clientY - element.getBoundingClientRect().top;
            this.dragAndDrop.selectedElement = { style: element.style, classList: element.classList.toString() };
            this.dragAndDrop.itemIndex = e.target.id;

            // Append Cloned Element to Page and Modify Style
            const clonedElement = element.cloneNode(true);
            clonedElement.id = `cloned-${element.id}`;
            document.body.append(clonedElement);
            this.clonedElement = document.getElementById(`cloned-${element.id}`);
            this.clonedElement.classList.add('item-clone');
            this.clonedElement.classList.add('no-animation');
            this.clonedElement.style.left = `${e.clientX + 25}px`;
            this.clonedElement.style.top = `${e.clientY - this.dragAndDrop.shiftY}px`;

            // Modify Current Element
            element.style.pointerEvents = 'none';
            element.style.setProperty('border', '2px dashed rgba(255, 255, 255, 0.5)', 'important');
            element.style.setProperty('opacity', '0.2', 'important');
            element.classList.add('grey', 'darken-4');
            element.classList.remove('grey', 'darken-3');

            // Toggle Event Listeners
            document.addEventListener('mouseup', this.dropItem);
            document.addEventListener('mouseover', this.mouseOver);
            document.addEventListener('mousemove', this.updatePosition); // This calls UpdatePosition
        },
        updatePosition(e) {
            this.clonedElement.style.left = `${e.clientX + 25}px`;
            this.clonedElement.style.top = `${e.clientY - this.dragAndDrop.shiftY}px`;
        },
        mouseOver(e) {
            if (this.lastHoverID) {
                const element = document.getElementById(this.lastHoverID);
                element.style.removeProperty('border');
                this.lastHoverID = null;
            }

            if (!e || !e.target || !e.target.id || e.target.id === '') {
                return;
            }

            if (this.lastHoverID !== e.target.id) {
                const element = document.getElementById(e.target.id);
                element.style.setProperty('border', '2px dashed white', 'important');
                this.lastHoverID = e.target.id;
            }
        },
        async dropItem(e) {
            this.dragging = false;

            document.removeEventListener('mouseover', this.mouseOver);
            document.removeEventListener('mouseup', this.dropItem);
            document.removeEventListener('mousemove', this.updatePosition);

            if (this.lastHoverID) {
                const element = document.getElementById(this.lastHoverID);
                element.style.removeProperty('border');
                element.style.removeProperty('box-shadow');
                this.lastHoverID = null;
            }

            this.clonedElement.remove();

            const selectElement = document.getElementById(this.dragAndDrop.itemIndex);
            selectElement.style = this.dragAndDrop.selectedElement.style;
            selectElement.style.pointerEvents = 'all';
            selectElement.classList.remove(...selectElement.classList);
            selectElement.classList.add(...this.dragAndDrop.selectedElement.classList.split(' '));

            this.x = 0;
            this.y = 0;

            if (!e || !e.target || !e.target.id || e.target.id === '') {
                return;
            }

            const selectedSlot = this.dragAndDrop.itemIndex;
            const endSlot = e.target.id;

            const endElement = document.getElementById(endSlot);
            const isTab = endElement.id.includes('tab');
            if (!isTab) {
                if (!endElement.classList.contains('is-null-item')) {
                    return;
                }
            }

            if (selectedSlot === endSlot) {
                return;
            }

            const hash = selectElement.dataset.hash ? `${selectElement.dataset.hash}` : null;

            if ('alt' in window) {
                alt.emit('inventory:Process', { selectedSlot, endSlot, tab: this.pageIndex, hash });
            }

            if (!isTab) {
                await this.updateLocalData(selectedSlot, endSlot);
            }
        },
        updateLocalData(selectedSlot, endSlot) {
            const selectIndex = this.stripCategory(selectedSlot);
            const endIndex = this.stripCategory(endSlot);

            const selectName = this.getDataName(selectedSlot);
            const endName = this.getDataName(endSlot);

            const selectItems = [...this[selectName]];
            const endItems = [...this[endName]];

            const selectItem = this.removeLocalItem(selectIndex, selectName === 'inventory', selectItems);
            const endItem = this.removeLocalItem(endIndex, endName === 'inventory', endItems);

            if (endItem) {
                endItem.slot = selectIndex;
            }

            selectItem.slot = endIndex;

            this.replaceLocalData(endIndex, selectItem, endName === 'inventory', endItems);
            this.replaceLocalData(selectIndex, endItem, selectName === 'inventory', selectItems);

            const selectFunctionUpdater = `update${this.capitalizeFirst(selectName)}`;
            const endFunctionUpdater = `update${this.capitalizeFirst(endName)}`;

            this[selectFunctionUpdater](selectItems);
            this[endFunctionUpdater](endItems);
        },
        capitalizeFirst(value) {
            return value.charAt(0).toUpperCase() + value.slice(1);
        },
        getDataName(prefix) {
            if (prefix.includes('tab-')) {
                return 'tab';
            }

            if (prefix.includes('i-')) {
                return 'inventory';
            }

            if (prefix.includes('g-')) {
                return 'ground';
            }

            if (prefix.includes('e-')) {
                return 'equipment';
            }

            return 'toolbar';
        },
        removeLocalItem(index, isInventory = false, localArray) {
            if (isInventory) {
                const itemClone = localArray[this.pageIndex][index];
                localArray[this.pageIndex][index] = null;
                return itemClone;
            }

            const itemClone = localArray[index];
            localArray[index] = null;
            return itemClone;
        },
        replaceLocalData(index, replacementItem, isInventory = false, localArray) {
            if (isInventory) {
                localArray[this.pageIndex][index] = replacementItem;
                return;
            }

            localArray[index] = replacementItem;
        },
        stripCategory(value) {
            return parseInt(value.replace(/.-/gm, ''));
        },
        handleClose(keyPress) {
            if (keyPress.key !== 'Escape') {
                return;
            }

            document.removeEventListener('keyup', this.handleClose);

            setTimeout(() => {
                if ('alt' in window) {
                    alt.emit('inventory:Close');
                }
            }, 50);
        }
    },
    computed: {
        getInventory() {
            return this.inventory[this.pageIndex];
        },
        getItemInfo() {
            if (this.itemInfo.includes('g-')) {
                return this.ground[parseInt(this.itemInfo.replace('g-', ''))];
            }

            if (this.itemInfo.includes('t-')) {
                return this.toolbar[parseInt(this.itemInfo.replace('t-', ''))];
            }

            if (this.itemInfo.includes('e-')) {
                return this.equipment[parseInt(this.itemInfo.replace('e-', ''))];
            }

            return this.inventory[this.pageIndex][parseInt(this.itemInfo.replace('i-', ''))];
        },
        getItemProperties() {
            if (this.itemInfo.includes('g-')) {
                const target = this.ground[parseInt(this.itemInfo.replace('g-', ''))];

                if (!target || !target.data) {
                    return null;
                }

                return Object.keys(target.data).map((key) => {
                    return { key, value: target.data[key] };
                });
            }

            if (this.itemInfo.includes('t-')) {
                const target = this.toolbar[parseInt(this.itemInfo.replace('g-', ''))];

                if (!target || !target.data) {
                    return null;
                }

                return Object.keys(target.data).map((key) => {
                    return { key, value: target.data[key] };
                });
            }

            if (this.itemInfo.includes('e-')) {
                const target = this.equipment[parseInt(this.itemInfo.replace('e-', ''))];

                if (!target || !target.data) {
                    return null;
                }

                return Object.keys(target.data).map((key) => {
                    return { key, value: target.data[key] };
                });
            }

            const target = this.inventory[this.pageIndex][parseInt(this.itemInfo.replace('i-', ''))];

            if (!target || !target.data) {
                return null;
            }

            return Object.keys(target.data).map((key) => {
                return { key, value: target.data[key] };
            });
        }
    },
    mounted() {
        document.addEventListener('keyup', this.handleClose);

        // Used to populate data on entry.
        this.inventory = new Array(6).fill(new Array(28).fill(null));
        this.ground = new Array(8).fill(null);
        this.equipment = new Array(9).fill(null);
        this.toolbar = new Array(4).fill(null);

        if ('alt' in window) {
            alt.on('inventory:Toolbar', this.updateToolbar);
            alt.on('inventory:Inventory', this.updateInventory);
            alt.on('inventory:Equipment', this.updateEquipment);
            alt.on('inventory:Ground', this.updateGround);
            alt.emit('inventory:Update');
            alt.emit('ready');
        } else {
            // Debug / Development Mode
            const items = [];
            for (let i = 0; i < 5; i++) {
                items.push({
                    name: `An Item ${i}`,
                    uuid: `some_hash_thing_${i}`,
                    slot: i,
                    description: `words`,
                    icon: 'crate',
                    quantity: Math.floor(Math.random() * 10),
                    weight: Math.floor(Math.random() * 5),
                    data: {
                        water: 100
                    }
                });
            }

            const slot2 = [
                {
                    name: `Sack`,
                    uuid: `some_hash_thing_27`,
                    slot: 27,
                    description: `It's a sack and it doesn't do much other than sack around. What a lazy sack.`,
                    icon: 'sack',
                    quantity: Math.floor(Math.random() * 10),
                    weight: Math.floor(Math.random() * 5),
                    data: {
                        water: 100
                    }
                }
            ];

            const ground = [
                {
                    position: { x: 0, y: 0, z: 0 },
                    item: {
                        name: `Gun`,
                        uuid: `some_hash_thing_ground`,
                        description: `Forbidden pez dispenser go brrr.`,
                        icon: 'gun',
                        quantity: 1,
                        weight: 2,
                        hash: '490218490129012',
                        data: {
                            bang: true,
                            ammo: 25,
                            'gluten-free': true,
                            owner: 'some_guy_off_main',
                            condition: 50,
                            rarity: `A+`
                        }
                    }
                }
            ];

            this.updateToolbar([]);
            this.updateGround(ground);
            this.updateInventory([items, slot2, [], [], [], [], []]);
            this.updateEquipment([
                {
                    name: `Hat`,
                    uuid: `some_hash_thing_ground`,
                    description: `What a cozy hat! Wow. Much cozy. Many comforts.`,
                    icon: 'hat',
                    quantity: Math.floor(Math.random() * 10),
                    weight: Math.floor(Math.random() * 5)
                }
            ]);
        }
    }
};
</script>
