<script>
// Test Page
import home from './components/Home';

// Actual Pages
import atm from './components/ATM/ATM';
import characters from './components/Characters/Characters';
import creator from './components/Creator/Creator';
import inventory from './components/Inventory/Inventory';
import hud from './components/Hud/Hud';

export default {
    name: 'app',
    components: {
        home,
        atm,
        characters,
        creator,
        inventory,
        hud
    },
    data() {
        return {
            component: null
        };
    },
    computed: {
        altReady() {
            if ('alt' in window) {
                return true;
            }

            return false;
        }
    },
    methods: {
        goTo(componentName) {
            console.log(`Going to route: ${componentName}`);
            this.component = componentName;
        }
    },
    created() {
        this.$root.$on('route', this.goTo);
    },
    mounted() {
        console.log('we fucking mounted boiii');

        if ('alt' in window) {
            alt.on('route', this.goTo);
            alt.emit('ready');
        }
    }
};

